* {
    font-family: sans-serif;
    font-size: medium;
    margin: 0%;
}

html {
    height: 100%;
}

body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 20px 0 20px;
    background-color: bisque;
    border-bottom: 1px solid black;
    margin-bottom: 25px;
}

footer {
   margin-top: auto;
}

header h1 {
    font-size: x-large;
    font-weight: bolder;
}

.portfolio-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.name-title, .header-description {
    display: flex;
    margin: 10px, 30px;
    padding: 20px;
}

.header-description {
    border-bottom: 2px solid black;
}

